.articleContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
    height: 100%;
    text-align: center;
    margin-top: 30px;
}

.articleDiv {
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin: 10px auto;
    width: 400px;
    height: 250px;
    display: block;
    background-color: white;
    border: 3px solid #b99877;
    padding: 30px;
    position: relative;
    -webkit-box-shadow: 20px 20px 20px -6px #666666;
            box-shadow: 20px 20px 20px -6px #666666;
}

.articleDiv:hover{
    -webkit-transform:scale(1.08);
        -ms-transform:scale(1.08);
            transform:scale(1.08);
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-box-shadow: 20px 20px 20px -6px #666666;
            box-shadow: 20px 20px 20px -6px #666666;

}

.articleTitle {
    font-family: "workSans";
    font-size: 18px;
    line-height: 23px;
    font-weight: 700;
    letter-spacing: 1px;
    margin-top: 10px;
    margin-bottom: 10px;
    text-transform: uppercase;
    position: relative;
    vertical-align: baseline;
    text-shadow: 1px 1px #666666;
}

.articleAuthor {
    color: #b99877;
    font-size: 1.1rem;
    line-height: 1.4375rem;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    position: relative;
    vertical-align: baseline;
    margin: 15px;
    text-shadow: 1px 1px white;
    text-align: left;
}

.articleContent {
    color: #666666;
    font-size: 1rem;
    font-weight: 700;
    text-transform: lowercase;
    position: relative;
    vertical-align: baseline;
    margin: 20px;
    margin-bottom: 5%;
    font-style: italic;
    text-align: left;
}

.articleUrl {
    color: #666666;
    font-size: 10px;
    font-weight: 700;
    text-transform: lowercase;
    position: relative;
    vertical-align: baseline;
    margin: 20px;
    margin-bottom: 5%;
    font-style: italic;
    text-align: left;
}

.articleUrl a {
    color: #666666;
    text-decoration: none;
}



